/********** Main **********/
.App {
  text-align: center;
  font-size: larger;
  min-height: 100vh;
}

/********** Content **********/
.container {
  margin: auto;
  font-size:x-large;
  display: flex;
  justify-content: center;
  padding-bottom: 4rem;
}

.content {
  width: 70vw;
}

#firstSection {
  display: flex;
  align-items: center;
}

#lastSection {
  padding-bottom: 4rem;
}

/********** Header **********/
.App-header {
  border-bottom: 3px solid #A89870;
  min-height: 150px;
  display: flex;
  align-items: flex-start; 
  padding-top: 20px;
  justify-content: center;
  color: black;
}

.header-content {
  width: 70vw;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.header-content ul {
  list-style: none;
  text-align: right;
}

/********** Footer **********/
.App-footer {
  position: fixed;
  bottom: 0;
  height: 4rem;
  border-top: 3px solid #A89870;
  /* text-align: left; */
  color: black;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: white;
}

.footer-content {
  width: 70vw; 
  display: flex;
  justify-content: space-between;
  padding: 5px;
}

/********** Logo **********/
.App-logo {
  height: 100px;
  pointer-events: none;
}

/********** Sidebar **********/
.sidebar {
  position: fixed;
  right: 0px;
  min-width: 100px;
  align-items: right; 
  width: 5vw;
}

.sidebar ul {
  list-style: none;
  margin: 100px 0 0 0;
  justify-content: space-between;
}

.sidebar ul li {
  padding: 30px 0;
}